<template>
    <UiInput
        type="text"
        label="    "
        placeholder="  " 
        v-model="formModel.name"
    />
    <UiInput
        type="date"
        label="   "
        v-model="formModel.birthday" 
    />
    <UiInput
        type="phone"
        label="  "
        placeholder="+7 ___ ___ - __ - __" 
        v-model="formModel.phone" 
    />
    <UiFileAttachment
        label=""
        placeholderTip=""
        @handleFileChange="(...args) => onFileChange('passport', ...args)"
    />
    <UiFileAttachment
        label=" "
        placeholderTip=""
        @handleFileChange="(...args) => onFileChange('license', ...args)"
    />
    <UiFileAttachment
        label="  "
        :isPhotoAttachment="true"
        placeholderTip=""
        @handleFileChange="(...args) => onFileChange('photo', ...args)"
    />
</template>

<script>

import UiInput from '@/components/ui/UiInput.vue'
import UiFileAttachment from '@/components/ui/UiFileAttachment.vue'
import { computed } from 'vue'


export default {
    components: { 
        UiInput, 
        UiFileAttachment
    },
    props: {
        modelValue: {
            type: Object,
            default: () => ({}),
        }
    },
    emits: ['updateFile'],
    setup(props, { emit }) {
        const formModel = computed({ 
            get: () => props.modelValue,
            set: (value) => emit('update:modelValue', value),
        })

        const onFileChange = (key, file) => {
            emit('updateFile', key, file)
        }

        return {
            formModel,
            onFileChange
        }
    }
}
</script>

